import React from 'react'
import posed from 'react-pose'
import { tween } from 'popmotion'
import { interpolate } from 'flubber'
import styled from 'styled-components'

const StyledBody = styled.div`
  height: 40vh;
  padding: 100px 0;
  flex-flow: column;
  justify-content: stretch;
  align-items: center;
  display: flex;
  background: #f4f3fa;

  /* #root {
    color: #fff;
    font-size: 32px;
    font-family: PT Sans, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 200;
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
  } */

  button {
    display: flex;
    border: none;
    margin-bottom: 20px;
    width: auto;
    overflow: visible;
    background: transparent;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
    color: white;
    padding: 15px 20px;
    cursor: pointer;
    color: rgb(70, 200, 120);
    font-size: 2rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
  }
`

const paths = {
  plane:
    'M187.57,0C291.16,0,375.13,67.75,375.13,151.32c0,33.35-13.41,64.17-36.07,89.17l34.85,86.65L226.21,297.46c-15.43.42-32.72,4.16-50,4.61-2,.05-3.92.08-5.86.08C69.88,302.15,0,233.3,0,151.32,0,67.75,84,0,187.57,0m0,41C106.76,41,41,90.51,41,151.32c0,62.6,55.57,109.8,129.28,109.8q2.38,0,4.78-.06c6.93-.18,14.5-1.12,22.52-2.12,8.87-1.1,18-2.24,27.49-2.5l4.64-.13,4.56.92,73.19,14.71L301,255.81,291.41,232l17.25-19c11.61-12.81,25.44-33.89,25.44-61.62C334.1,90.51,268.37,41,187.57,41Z',
  heart:
    'M255,489.6l-35.7-35.7C86.7,336.6,0,257.55,0,160.65C0,81.6,61.2,20.4,140.25,20.4c43.35,0,86.7,20.4,114.75,53.55 C283.05,40.8,326.4,20.4,369.75,20.4C448.8,20.4,510,81.6,510,160.65c0,96.9-86.7,175.95-219.3,293.25L255,489.6z',
  bookmark:
    'M357,0H102C73.95,0,51,22.95,51,51v408l178.5-76.5L408,459V51C408,22.95,385.05,0,357,0z',
}

const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.8 },
})

class IconMorph extends React.Component {
  state = { pathIndex: 0 }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg width="150" height="150" viewBox="0 0  375.13 327.15">
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.gotoNext}>
          Refreshingly Simple Text Message Appointment Reminders
        </NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
